.chatBotPage{
    width: 50%;
    margin: 5% auto;
}

.chatBotPage input{
    width: 80%;
    border-radius: 25px;
    padding: 2%;
}


.chatBotPage button{
    padding: 2%;
    font: 30px;
    font-weight: bold;
    width: 10%;
    border-radius: 25px;
    /* height: 40px; */
    background-color: blue;
    color: white;
    cursor: pointer;
}

.tagline{
    font-size: x-large;
    text-align: center;
    padding: 5px;
}

.inputSection{
    margin: 5% auto;
}




/* MEDIA QUERIES */


/*  Mobile  */
@media (max-width: 600px) {
  .chatBotPage {
    width: 90%;
    margin: 20% auto;
  }

  .chatBotPage input {
    width: 100%;
    margin-bottom: 10px;
    padding: 3%;
    font-size: 1rem;
  }

  .chatBotPage button {
    width: 100%;
    padding: 3%;
    font-size: 1rem;
  }

  .inputSection {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .tagline {
    font-size: large;
    padding: 10px;
  }
}


/*  Tablet  */
@media (max-width: 900px) {
  .chatBotPage {
    width: 75%;
  }

  .chatBotPage input {
    width: 85%;
  }

  .chatBotPage button {
    width: 30%;
    font-size: 1rem;
  }

  .tagline {
    font-size: x-large;
  }
}
