.logoutPage{
    text-align: center;
    margin: 5% auto;
    width: 50%;
    font-size: x-large;
}

.btnSection{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.logoutBtn{
    font-size: x-large;
    width: 25%;
    color: white;
}

.yesBtn{
    background-color: red;
}

.noBtn{
    background-color: green;
}


/* MEDIA QUERIES */


/*  Mobile Devices */
@media (max-width: 600px) {
  .logoutPage {
    width: 90%;
    font-size: large;
  }

  .btnSection {
    flex-direction: column;
    gap: 1rem;
    margin-top: 2rem;
  }

  .logoutBtn {
    width: 70%;
    font-size: large;
  }
}


/*  Tablets  */
@media (max-width: 900px) {
  .logoutPage {
    width: 80%;
    margin: 20% auto;
  }

  .logoutBtn {
    width: 40%;
    font-size: large;
  }
}
